.profile-info {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  margin: auto 0;
  padding-left: 30px;
  max-width: calc(100% - 300px);
}

@media (max-width: 613px) {
  .profile-info {
    margin: 26px 0 0;
    padding-left: 0;
    max-width: 100%;
    justify-content: center;
  }
}